<template>
  <v-container>
  <v-data-table
    :headers="headers"
    :items="desserts"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar flat color="white">
        <v-toolbar-title>我的用户</v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark class="mb-2" v-on="on">新增用户</v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="12">
                    <v-text-field v-model="editedItem.agent_phone" label="代理人手机"></v-text-field>
                  </v-col>                  
                  <v-col cols="12" sm="12">
                    <v-text-field v-model="editedItem.phone" label="手机"></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">取消</v-btn>
              <v-btn color="blue darken-1" text @click="save">保存</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.action="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        edit
      </v-icon>
      <v-icon
        small
        @click="deleteItem(item)"
      >
        delete
      </v-icon>
    </template>
    <template v-slot:no-data>
      <v-btn color="primary" @click="initialize">刷新</v-btn>
    </template>
  </v-data-table>
  </v-container>
</template>

<script>
export default {
 data: () => ({
      dialog: false,
      headers: [
        {
          text: '姓名',
          align: 'start',
          value: 'name',
        },
        { text: '代理人手机', align: 'center', value: 'agent_phone' },
        { text: '手机', align: 'center', value: 'phone' },
        { text: '编辑', align: 'end', value: 'action', sortable: false },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        agent_phone:'',
        phone: '',
      },
      defaultItem: {
        name: '',
        agent_phone:'',
        phone: '',
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? '新增用户' : '变更代理人'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        var url = this.$domain + "/api/qm/user?agent_phone="+'&offset=0&limit=10000'
        this.$get(url)
        .then((res)=>{
          window.console.log(res)
          if(res.code == 1){
            this.desserts = res.data
          }else{
             window.alert(res.msg+":"+res.err)
          }
          this.$store.commit('setProgress',false)
        })
        .catch((err)=>{
          window.alert(err)
          this.$store.commit('setProgress',false)
        })
      },

      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        const index = this.desserts.indexOf(item)
        if(confirm('确认要删除此用户?')){
          this.$store.commit('setProgress',true)
          var url = this.$domain+"/api/qm/admin/user?phone="+item.phone
          this.$delete(url)
          .then((res)=>{
          window.console.log(res)
          if(res.code == 1){
            this.desserts.splice(index, 1)
          }else{
             window.alert(res.msg+":"+res.err)
          }
            this.$store.commit('setProgress',false)
          })
          .catch((err)=>{
            window.alert(err)
            this.$store.commit('setProgress',false)
          })
        }
      },

      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },

      save () {
        if (this.editedIndex > -1) {
          // 编辑
          let url = this.$domain+'/api/qm/admin/user?phone='+this.editedItem.phone+"&agent_phone="+this.editedItem.agent_phone
          this.$put(url)
          .then((res)=>{
          window.console.log(res)
          if(res.code == 1){
            Object.assign(this.desserts[this.editedIndex], this.editedItem)
          }else{
              window.alert(res.msg+":"+res.err)
          }
            this.$store.commit('setProgress',false)
          })
          .catch((err)=>{
            window.alert(err)
            this.$store.commit('setProgress',false)
          })
          
        } else {
          // 新增
          let url = this.$domain+'/api/qm/admin/user'
          var data = {
              "agent_phone":this.editedItem.agent_phone,
              "name":this.editedItem.name,
              "phone":this.editedItem.phone,
              "password":"001122"
              }
          this.$store.commit('setProgress',true)
          this.$post(url,data)
          .then((res)=>{
          window.console.log(res)
          if(res.code == 1){
            this.desserts.push(res.data)
          }else{
             window.alert(res.msg+":"+res.err)
          }
            this.$store.commit('setProgress',false)
          })
          .catch((err)=>{
            window.alert(err)
            this.$store.commit('setProgress',false)
          })
        }
        this.close()
      },
    },
}
</script>

