<template>
  <v-app id="inspire">
    <v-progress-linear
      indeterminate
      color="cyan"
      v-show="$store.state.progress"
    ></v-progress-linear>
    <Toolbar v-show="$store.state.login.status"/>
    <AdminUserPage v-if="$store.state.page == 'admin_user'"/>
    <AdminAgentPage v-if="$store.state.page == 'admin_agent'"/>
    <AgentUserPage v-if="$store.state.page == 'agent_user'"/>
    <Login v-show="!$store.state.login.status"/>
  </v-app>
</template>

<script>
import Login from './Login'
import Toolbar from './components/Toolbar'
import AdminUserPage from './view/AdminUserPage'
import AdminAgentPage from './view/AdminAgentPage'
import AgentUserPage from './view/AgentUserPage'


export default {
  name: 'App',

  components: {
    Login,
    Toolbar,
    AdminUserPage,
    AdminAgentPage,
    AgentUserPage
  },

  data: () => ({

  }),

  created: function(){
    if(this.$cookies.get("qm_jwt_token") && this.$cookies.get("qm_jwt_token_class")){
      this.$store.commit('setLogin',{status:true,class:this.$cookies.get("qm_jwt_token_class")})
    }
    this.$store.commit('setProgress',false)
  }

};
</script>
