import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    login:{
      status:false,
      class:""
    },
    page:'',
    progress:true,
    agent_phone:''
  },
  mutations: {
    setLogin(state, data){
      state.login = data
    },
    setProgress(state,data){
      state.progress= data
    },
    setPage(state,data){
      state.page = data
    },
    setAgentPhone(state,data){
      state.agent_phone = data
    }
  },
  actions: {
  },
  modules: {
  }
})
