import axios from 'axios';
import Cookies from 'js-cookie'

axios.defaults.timeout = 5000;
// axios.defaults.baseURL = 'https://api.eheal.com.cn/medical-nutrition';
axios.defaults.headers = {
  'Content-Type': 'application/json'
}

