//http request 拦截器
axios.interceptors.request.use(
  config => {
    const token = Cookies.get('qm_jwt_token'); // 注意使用的时候需要引入cookie方法，推荐js-cookie
    config.headers = {
      'qm_jwt_token': token
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);


//http response 拦截器
axios.interceptors.response.use(
  response => {
    //按需要加判断
    return response;
  },
  error => {
    return Promise.reject(error)
  }
)


/**
 * 封装 GET 方法
 * @param url
 * @param data
 * @returns {Promise}
 */

export function GET (url, params = {}) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
      params: params
    })
      .then(response => {
        resolve(response.data);
      })
      .catch(err => {
        reject(err)
      })
  })
}

/**
 * 封装 POST 请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function POST (url, data = {}) {
  return new Promise((resolve, reject) => {
    axios.post(url, data)
      .then(response => {
        resolve(response.data);
      }, err => {
        reject(err)
      })
  })
}


/**
 * 封装 DELETE 请求
 * @param url
 * @param config
 * @returns {Promise}
 */
export function DELETE (url, param = {}) {
  return new Promise((resolve, reject) => {
    axios.delete(url, param)
      .then(response => {
        resolve(response.data);
      }, err => {
        reject(err)
      })
  })
}

/**
* 封装 PUT 请求
* @param url
* @param data
* @returns {Promise}
*/

export function PUT (url, data = {}) {
  return new Promise((resolve, reject) => {
    axios.put(url, data)
      .then(response => {
        resolve(response.data);
      }, err => {
        reject(err)
      })
  })
}