<template>
<div>
  <v-app id="inspire">
    
    <v-content>
      <v-container
        fluid
        fill-height
      >
        <v-layout
          align-center
          justify-center
        >
          <v-flex
            xs12
            sm8
            md4
          >
            <v-card class="elevation-12">
              <v-toolbar
                color="primary"
                dark
                flat
              >
                <v-toolbar-title>管理员登陆</v-toolbar-title>
                <v-spacer></v-spacer>
                
              </v-toolbar>
              <v-card-text>
                <v-form>
                  <v-select
                    :items="loginclass"
                    v-model="form.class"
                    label="登陆系统"
                  ></v-select>
                  <v-text-field
                    v-model="form.phone"
                    label="手机"
                    name="phone"
                    type="text"
                  ></v-text-field>

                  <v-text-field
                    v-model="form.password"
                    label="密码"
                    name="password"
                    type="password"
                  ></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="login">登陆</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</div>
</template>

<script>

export default {
    data: () => ({
      loginclass: ['管理员', '代理人'],
      form:{
        class:'',
        phone:'',
        password:''
      },
      condition:false
    }),
    methods:{
      login(){
        var url = ''
        var cls = ''
        if(this.form.class=='管理员'){
          url = this.$domain+'/api/qm/auth/admin/login'
          cls='admin'
        }
        if(this.form.class=='代理人'){
          url = this.$domain+'/api/qm/auth/agent/login'
          cls = 'agent'
        }
        if(url == ''){
          window.alert('请选择登陆系统')
          return
        }
        let data = {
          phone: this.form.phone,
          password: this.form.password
        }
        this.$store.commit('setProgress',true)
        this.$post(url,data)
        .then((res)=>{
          window.console.log(res)
          if(res.code == 1){
            this.$cookies.set("qm_jwt_token",res.data,7)
            this.$cookies.set("qm_jwt_token_class",cls,7)
            this.$store.commit('setLogin',{status:true,class:cls})
            if(cls=='agent'){
              this.$store.commit('setAgentPhone', this.form.phone)
            }
          }else{
             window.alert(res.msg+":"+res.err)
          }
          this.condition = false
          this.$store.commit('setProgress',false)
        })
        .catch((err)=>{
          window.alert(err)
          this.condition = false
        })
      }
    }
}
</script>

